import './styles/App.css'
import {useEffect, useState} from "react";
import PatientList from "./components/PatientList";
import axios from "axios";

function App() {

    const [patients, setPatients] = useState([{id: 1, first_name: 'asdasd', last_name: 'asdasdsdasd'}])

    useEffect(() => {
        console.log('asddasasd')
        const arr = [
            {id: 2, first_name: 'asdaaaa', last_name: 'asdasd1sdasd', date_birth: '15-21-2021'},
            {id: 3, first_name: 'as2dasd', last_name: 'asdasdsdas2d', date_birth: '15-21-2021'},
            {id: 4, first_name: 'asd3asd', last_name: 'asdasdsda3sd', date_birth: '15-21-2021'},
        ]
        getListsPatients()
        
    }, [])

    const getListsPatients = async () => {
        const response = await axios.get('http://127.0.0.1:8000/api-patients/patient-list/')
        setPatients(response.data)
    }

    return (
        <div className="App">
            <div className="panels">
                <PatientList patients={patients}/>
                <div className="right-pane">
                </div>
            </div>
        </div>
    );
}

export default App;


